@import url(../../bower_components/normalize-css/normalize.css);
@import url(../../bower_components/ResponsiveGridSystem/css/col.css);
/* VARIABLES */
/* ----------------------------------------- */
/* BASIC SETUP */
/* ----------------------------------------- */
/** Page Loader **/
.pace-running .entire-content {
  opacity: 0;
  transition: all 2s ease-in;
}

.pace-done .entire-content {
  opacity: 1;
}

/** Theme Name: - loading-bar **/
.pace .pace-progress {
  border-radius: 2px;
  transform: translate3d(0, 0, 0);
  display: block;
  position: absolute;
  right: 100%;
  margin-right: -7px;
  width: 93%;
  top: 7px;
  height: 14px;
  font-size: 12px;
  background: #34495e;
  color: #34495e;
  line-height: 60px;
  font-weight: bold;
  font-family: "Lato", sans-serif;
}

/* ----------------------------------------- */
/* GLOBAL SETTINGS */
/* ----------------------------------------- */
/* -- Global Reset -- */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  background-color: #fff;
  color: #555;
  font-family: 'Lato', 'Arial', sans-serif;
  font-weight: 300;
  font-size: 20px;
  text-rendering: optimizeLegibility;
}

.row {
  max-width: 1140px;
  margin: 0 auto;
}

/* REUSEABLE COMPONENTS */
/* ----------------------------------------- */
/* ------------------------------------------------ */
/* ------------------------------------------------ */
.btn:link,
.btn:visited {
  display: inline-block;
  padding: 10px 30px;
  font-weight: 300;
  text-decoration: none;
  border-radius: 50px;
  transition: all 0.3s ease-in;
}

.btn-full:link,
.btn-full:visited {
  background-color: #e76e22;
  border: 1px solid #e76e22;
  color: #fff;
  margin-right: 15px;
}

.btn-ghost:link,
.btn-ghost:visited {
  border: 1px solid #e76e22;
  color: #e76e22;
}

.btn:hover,
.btn:active {
  background-color: #cf6d17;
}

.btn-full:hover,
.btn-full:active {
  border: 1px solid #cf6d17;
}

.btn-ghost:hover,
.btn-ghost:active {
  border: 1px solid #cf6d17;
  color: #fff;
}

/* ------------------------------------------------ */
/* ------------------------------------------------ */
h1 {
  margin-top: 0;
  margin-bottom: 20px;
  color: #fff;
  font-size: 240%;
  text-transform: uppercase;
  font-weight: 300;
  letter-spacing: 1px;
  word-spacing: 4px;
}

/* SECTIONS */
/* ----------------------------------------- */
/* ------------------------------------------------ */
/* ------------------------------------------------ */
.site-header {
  background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url(/public/images/header/hero.jpg);
  background-size: cover;
  background-position: center;
  height: 100vh;
  /* ------------------------------------------------ */
  /* ------------------------------------------------ */
  /* ------------------------------------------------ */
  /* ------------------------------------------------ */
  /* ------------------------------------------------ */
  /* ------------------------------------------------ */
}

.site-header .logo {
  height: 100px;
  width: auto;
  float: left;
  margin-top: 20px;
}

.site-header .main-nav {
  float: right;
  list-style: none;
}

.site-header .main-nav li {
  display: inline-block;
  margin-left: 40px;
  margin-top: 63px;
}

.site-header .main-nav li a:link,
.site-header .main-nav li a:visited {
  padding: 8px 0;
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 80%;
  border-bottom: 2px solid transparent;
  transition: all 0.3s ease-in;
}

.site-header .main-nav li a:hover,
.site-header .main-nav li a:active {
  border-bottom: 2px solid #e67e22;
}

.site-header .hero-text-box {
  /**  METHOD 1 - Centering Text Over Image  **/
  /**  METHOD 2 - Centering Text Over Image  **/
  position: absolute;
  width: 1140%;
  top: 50%;
  left: 50%;
  transform: translateY(-50%, -50%);
}

/* SECTIONS */
/* ----------------------------------------- */
.slicknav_menu {
  display: none;
}

@media screen and (max-width: 992px) {
  #menu {
    display: none;
  }
  .slicknav_menu {
    display: inline-block;
    position: fixed;
    z-index: 1000;
    background: transparent;
    right: 0;
  }
}
